.card {
    margin:2em auto;
    max-width: 350px;
    min-height: 200px;
    background: #000;
}
.projectImg{
object-fit: contain !important;

}

.card .image {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.card .image img {
  width: 100%;
  transition: .5s;
}
.card:hover .image img {
  opacity: .5;
  transform: translateX(30%);/*100%*/
}
.card .details {
  position: absolute;
  top: 0;
  left: 0;
  width: 70%;
  height: 100%;
  background: #e3f2fd;
  transition: .5s;
  transform-origin: left;
  transform: perspective(2000px) rotateY(-90deg);
}
.card:hover .details {
  transform: perspective(2000px) rotateY(0deg);
}
.card .details .center {
  padding: 20px;
  text-align: center;
  background: #e3f2fd;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.card .details .center h1 {
  margin: 0;
  padding: 0;
  color:rgb(21, 11, 158);
  line-height: 20px;
  font-size: 20px;
  text-transform: uppercase;
}
.card .details .center span {
  font-size: 14px;
  color: #2962ff;
}
.card .details .center p {
  margin: 10px 0;
  padding: 0;
  color: #262626;
  font-size: 15px !important;
  font-weight: 100 !important;
}
.card .details .center ul {
  margin: 10px auto 0;
  padding: 0;
  display: table;
}
.card .details .center ul li {
  list-style: none;
  margin: 0 5px;
  float: left;
}
.card .details .center ul li a {
  display: block;
  width: 30px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  transform: .5s;
  text-decoration: none;
  color:#000;
}
.card .details .center ul li a:hover {
  color: cyan;
}
.cardR{
    border-radius: 4px;
    background: #fff;
    box-shadow: 0 6px 10px rgba(0,0,0,.08), 0 0 6px rgba(0,0,0,.05);
    transition: .3s transform cubic-bezier(.155,1.105,.295,1.12),.3s box-shadow,.3s -webkit-transform cubic-bezier(.155,1.105,.295,1.12);
  padding: 14px 10px 18px 20px;
  cursor: pointer;
}
.cardR p{
  font-size: 10px;
}
.cardR span{
  font-size: 10px;
  font-style: italic;
}
.cardR:hover{
     transform: scale(1.05);
  box-shadow: 0 10px 20px rgba(0,0,0,.12), 0 4px 8px rgba(0,0,0,.06);
}
.cardR img{
  position: absolute;
  top: 20px;
  right: 15px;
  max-height: 120px;
}

.cardR-1{
  background-image: url("media/resume101.jpg");
  background-repeat: no-repeat;
  background-position: right;
  background-size: cover;
}

@media(max-width: 990px){
  .cardR{
    margin: 20px;
  }
} 


