@keyframes bounceInRight {
  from,
  60%,
  90%,
  to {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }

  from {
    opacity: 0;
    -webkit-transform: translate3d(3000px, 0, 0);
    transform: translate3d(3000px, 0, 0);
  }

  40% {
    opacity: 1;
    -webkit-transform: translate3d(20px, 0, 0);
    transform: translate3d(20px, 0, 0);
  }

  90% {
    -webkit-transform: translate3d(5px, 0, 0);
    transform: translate3d(5px, 0, 0);
  }

  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

.bounceInRight {
  animation-name: bounceInRight;
}

@keyframes bounceInLeft {
  from,
  60%,
  to {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.355, 1);
    animation-timing-function: cubic-bezier(0.215, 0.61, 1);
  }
  
  0% {
    opacity: 0;
    -webkit-transform: translate3d(-3000px, 0, 0);
    transform: translate3d(-3000px, 0, 0);
  }
  
  60% {
    opacity: 1;
    -webkit-transform: translate3d(25px, 0, 0);
    transform: translate3d(-20px, 0, 0);
  }
  
  
  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

.bounceInLeft {
  animation-name: bounceInLeft;
}

.glow {
  animation-name: glow;
}

@keyframes glow {
  from {
    text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #4c4b9e, 0 0 40px #070864, 0 0 50px #280775, 0 0 60px #281be0, 0 0 70px #080b94;
  }
  
  to {
    text-shadow: 0 0 20px #fff, 0 0 30px #5373db, 0 0 40px #4c4b9e, 0 0 50px #070864, 0 0 60px #280775, 0 0 70px #281be0, 0 0 80px #080b94;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .glow {
    animation: glow 2s ease-in-out infinite alternate;
  }
  .App-logo1 {
    animation: bounceInLeft 2s;
  }
  .App-logo {
    animation: bounceInRight 2s;
  }
}


